"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import {
  Box,
  Button,
  Flex,
  Stack,
  Heading,
  Text,
  Input,
  IconButton,
  HStack,
  Field,
  Separator,
} from "@chakra-ui/react";
import { motion } from "framer-motion";
import { LuEye, LuEyeOff, LuUser, LuMail, LuLock } from "react-icons/lu";
import { FaGoogle, FaGithub } from "react-icons/fa";
import { useMutation } from "@tanstack/react-query";
import { toaster } from "../components/ui/toaster";

const MotionBox = motion.create(Box);

export default function SignUpPage() {
  const router = useRouter();
  const [showPassword, setShowPassword] = useState(false);
  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const signupMutation = useMutation({
    mutationFn: async (data: { name: string; email: string; password: string }) => {
      const res = await fetch("/api/auth/signup", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      });
      if (!res.ok) throw new Error("Signup failed");
      return res.json();
    },
    onSuccess: () => {
      toaster.create({
  title: "Success",
  description: "Logged in!",
  type: "success"
});
      router.push("/dashboard");
    },
    onError: (error: any) => {
      toaster.error(error.message || "Signup failed");
    },
  });

  const handleSignUp = (e: React.FormEvent) => {
    e.preventDefault();
    signupMutation.mutate({ name: fullName, email, password });
  };

  return (
    <Flex minH="100vh" align="center" justify="center" bg="gray.50" _dark={{ bg: "gray.950" }} p={6}>
      <MotionBox initial={{ opacity: 0, y: 20 }} animate={{ opacity: 1, y: 0 }} transition={{ duration: 0.5 }} style={{ width: "100%", maxWidth: "500px" }}>
        <Box p={8} bg="white" _dark={{ bg: "gray.900" }} boxShadow="2xl" borderRadius="3xl">
          <Stack gap={8}>
            <Stack align="center" textAlign="center" gap={2}>
              <Heading size="3xl" fontWeight="black">Get Started</Heading>
              <Text color="gray.500">Create your account in seconds.</Text>
            </Stack>

            <Stack gap={3} width="100%">
              <Button variant="outline" size="lg" width="100%" gap={3}>
                <FaGoogle color="#EA4335" /> Sign up with Google
              </Button>
              <Button variant="outline" size="lg" width="100%" gap={3}>
                <FaGithub /> Sign up with GitHub
              </Button>
            </Stack>

            <HStack width="100%">
              <Separator flex="1" />
              <Text fontSize="xs" color="gray.400" fontWeight="bold" whiteSpace="nowrap" px={2}>
                OR CONTINUE WITH EMAIL
              </Text>
              <Separator flex="1" />
            </HStack>

            <Box as="form" onSubmit={handleSignUp}>
              <Stack gap={4}>
                <Field.Root>
                  <Field.Label>Full Name</Field.Label>
                  <Input placeholder="John Doe" value={fullName} onChange={(e) => setFullName(e.target.value)} />
                </Field.Root>

                <Field.Root>
                  <Field.Label>Email</Field.Label>
                  <Input type="email" placeholder="name@example.com" value={email} onChange={(e) => setEmail(e.target.value)} />
                </Field.Root>

                <Field.Root>
                  <Field.Label>Password</Field.Label>
                  <Box position="relative">
                    <Input
                      type={showPassword ? "text" : "password"}
                      placeholder="••••••••"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                    />
                    <IconButton
                      aria-label="Toggle password"
                      variant="ghost"
                      position="absolute"
                      right="2"
                      top="50%"
                      transform="translateY(-50%)"
                      onClick={() => setShowPassword(!showPassword)}
                    >
                      {showPassword ? <LuEyeOff /> : <LuEye />}
                    </IconButton>
                  </Box>
                </Field.Root>

                <Button type="submit" colorScheme="teal" isLoading={signupMutation.isLoading}>
                  Create Account
                </Button>
              </Stack>
            </Box>
          </Stack>
        </Box>
      </MotionBox>
    </Flex>
  );
}
